@import url('https://cdnjs.cloudflare.com/ajax/libs/normalize/7.0.0/normalize.css');

/****Mobile****/
.contactContainer {
  background-color: #A4243B;
  display: grid;
  grid-template-columns: 1fr 1fr;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100vw;
  height: 10vw;
  position: relative;
}

.contactChild {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 50vw;
  height: 10vw;
}
.contactChild:hover {
  -webkit-transform: scale(.9);
      -ms-transform: scale(.9);
          transform: scale(.9);
}
.resumeContainer {
  font-size: 20px;
  color: #040403;
  font-family: 'Catamaran';
}
.LinkedInLogo {
  width: 20vw;
  height: 20vw;
  margin-top: 10px;
}
.GitHubLogo {
  width: 14vw;
  height: 5vw;
}

/****TABLET*******/
@media screen and (min-width: 545px) {
  .contactContainer {
    background-color: #A4243B;
    display: grid;
    grid-template-columns: 1fr 1fr;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    width: 100vw;
    height: 8vw;
    position: relative;
  }

  .contactChild {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 50vw;
    height: 8vw;
  }
  .contactChild:hover {
    -webkit-transform: scale(.9);
        -ms-transform: scale(.9);
            transform: scale(.9);
  }

  .LinkedInLogo {
  margin-top: 20px;
  width: 18vw;
  height: 18vw;
  }
  .resumeContainer {
    font-size: 20px;
    color: #040403;
    font-family: 'Catamaran';
  }
  .GitHubLogo {
    width: 13vw;
    height: 4vw;
  }
}

/******DESKTOP*****/
@media screen and (min-width: 769px) {

.contactContainer {
  background-color: #A4243B;
  display: grid;
  grid-template-columns: 1fr 1fr;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100vw;
  height: 4vw;
  position: relative;
}

.contactChild {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 50vw;
  height: 4vw;
}
.contactChild:hover {
  -webkit-transform: scale(.9);
      -ms-transform: scale(.9);
          transform: scale(.9);
}
.LinkedInLogo {
  width: 10vw;
  height: 10vw;
  margin-top: 10px;
}
.GitHubLogo {
  width: 11vw;
  height: 3vw;
}
.resumeContainer {
  font-size: 20px;
  color: #040403;
  font-family: 'Catamaran';
}
}

@media screen and (min-width: 1000px) {

  .GitHubLogo {
    width: 100px;
    height: 26px;
  }

}



