/****TABLET*******/
@media screen and (min-width: 545px) {
  .aboutMeContainer {
    height: 15vw;
    background-color: #34495E;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 100vw;
  }
  .aboutMeChildContainer {
    margin-left: 0;
    margin-right: -10px;
    width: 33vw;
  }
  /* entire container, keeps perspective */
  .flip-container {
  	-webkit-perspective: 1000px;
  	        perspective: 1000px;
  	-webkit-transform-style: preserve-3d;
  	        transform-style: preserve-3d;
  }
  	/*  UPDATED! flip the pane when hovered */
  	.flip-container:hover .back {
      opacity: 1;
      -webkit-transform:scale(1.03);
          -ms-transform:scale(1.03);
              transform:scale(1.03);
  		/*transform: rotateY(0deg);*/
  	}
  	.flip-container:hover .front {
      opacity: 0;
  	    /*transform: rotateY(180deg);*/
  	}

  .flip-container, .front, .back {
    width: 33vw;
  	height: 15vw;
  }

  /* flip speed goes here */
  .flipper {
  	-webkit-transition: 1s;
  	-o-transition: 1s;
  	transition: 1s;
  	-webkit-transform-style: preserve-3d;
  	        transform-style: preserve-3d;
  	position: relative;
    text-align: center;
  }

  /* hide back of pane during swap */
  .front, .back {
  	/*backface-visibility: hidden;*/
  	-webkit-transition: 1s;
  	-o-transition: 1s;
  	transition: 1s;
  	-webkit-transform-style: preserve-3d;
  	        transform-style: preserve-3d;
  	position: absolute;
  	top: 0;
  	left: 0;
  }

  /*  UPDATED! front pane, placed above back */
  .front {
  	z-index: 2;
    background-color: #34495E;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    color: #e6e6e6;
    /*transform: rotateY(0deg);*/
  }

  /* back, initially hidden pane */
  .back {
      background-color:  #e6e6e6;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
          align-items: center;
      -ms-flex-pack: center;
          justify-content: center;
      color: #484848;
      /*transform: rotateY(-180deg);*/
  }
  /* KEEP IN TOUCH */
  .kitback {
    color: #34495E;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: none;
  }
  .kitback h6 {
    font-weight: bold;
    font-size: 9px;
    margin-bottom: 0;
    margin-top: 0;
  }
  .kitback p {
    font-size: 8px;
  }
  .kitBorderBottom {
    border-bottom: thin solid #34495E;
    width: 28vw;
  }

  /* SKILLS */

  .skillsBack {
    color: #34495E;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    font-size: 8px;
    justify-items: center;

  }
  .backPone {
    border-bottom: thin solid #34495E;
    padding-bottom: 10px;
    width: 28vw;
  }
  .backPtwo {
    margin-top: -5px;
    width: 33vw;
    padding-bottom:5px;
  }
  .backPthree {
    border-top: thin solid #34495E;
    padding-top: 10px;
    margin-top: -15px;
    width: 28vw;
  }

  /* ABOUT */
  .aboutBack {
    line-height: 1.2;
    font-size: 8px;

  }
  .aboutBack p {
    width: 31vw;
  }
}
